const API_KEY = "29d327c32dc3a1c939eb4afde17beeee";
const BASE_PATH = "https://api.themoviedb.org/3";

//IGetPopular의 result배열
interface IPop {
  id: number;
  adult: boolean;
  backdrop_path: string;
  poster_path: string;
  title: string;
  overview: string;
  vote_average :number;
  release_date:string;
}
export interface IGetPopular {
  results :IPop[],
}


//IGetMovie의 result배열
interface IMovie {
  id: number
  backdrop_path: string;
  poster_path: string;
  title: string;
  overview: string;
}

export interface IGetMovie {
  dates: {
    maximum: string;
    minimum: string;
  };
  page: number;
  results: IMovie[];
  total_pages: number;
  total_results: number;
}
//IGetTv의 result배열
interface ITV {
  backdrop_path: string;
  first_air_date: string;
  genre_ids: number[];
  id: number;
  name: string;
  overview: string;
  poster_path: string;
}

export interface IGetTv {
      results :ITV[];
    
}

//getSearch interface
interface ISearch {
  media_type:string;
  gender : number;
  id : number;
  known_for: [{
    backdrop_path: string;
  }]
}

export interface IGetSearch {
  results: ISearch[];
  name: string;
  title: string;
  total_results: number;
}




//fetcher 함수


//Home >moviePopular
export function getPopular() {
  return fetch(`${BASE_PATH}/movie/popular?api_key=${API_KEY}`).then(
    (res)=>res.json()
  );
}

export function getMovies() {
  return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
    (res) => res.json()
  );
}


export function getTv(){
  return fetch(`${BASE_PATH}/tv/popular?api_key=${API_KEY}`)
  .then((res)=>res.json()
  );
}

//MultiSearch를 사용하면, tv, movie는 물론 사람까지 검색가능
export function getSearch(keyword : string){
  return fetch(`
  ${BASE_PATH}/search/multi?api_key=${API_KEY}&query=${keyword}&page=1`)
  .then((res)=>res.json()
  );
}


//movie detail
export function getMoviesDetail(movieId: number){
  return fetch(`${BASE_PATH}/movie/${movieId}?api_key=${API_KEY}`)
  .then((res)=>res.json()
  );
}