import React from "react";
import { useLocation } from "react-router-dom";
import { useQuery } from "react-query";
import { IGetSearch, getSearch } from "../api";
import styled from "styled-components";

const Wrapper = styled.div`
  background-color: black;
  margin: 100px 0 0 60px;
`;
const Loader = styled.div`
  height: 20vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Summury = styled.p`
  font-size: 20px;
  font-family: 'GmarketSansLight';
`;
const ShowBox = styled.div`
  margin-top: 25px;
  width: 85vw;
  height: 100vh;
  p {
    font-size: 19px;
    font-family: 'GmarketSansBold';
    text-align: center;
    width: 110px;
    padding: 7px 0;
    border-radius: 5px;
    background-color: ${(props) => props.theme.white.lighter};
    color: ${(props) => props.theme.black.darker};
  }
 
`;

const ShowMovie = styled.div`
 
`;

const ShowTv = styled.div``;

const ShowPerson = styled.div``;

function Search() {
  const location = useLocation();
  const keyword = new URLSearchParams(location.search).get("keyword");
  const { data, isLoading } = useQuery<IGetSearch>(["search", keyword], () =>
    getSearch("keyword")
  );
  return (
    <Wrapper>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <> 
        {(data === null ) ? <p>"We can't not find anything"</p> :
          <>
          <Summury>I found {data?.total_results} search results about {keyword}. </Summury>
                      {data?.results.map((el)=>
              <ShowBox>
              {el.media_type === "movie" ?  
              <ShowMovie> <p>Movies</p>  </ShowMovie> :
             <ShowTv> <p>Tv Show</p>  </ShowTv> }
              </ShowBox>)}
          </>
     } </>
      )}
    </Wrapper>
  );
}

export default Search;
